$primary-color: #2fa774;
$secondary-color: #213155;
$button-hover-color: #01153f;
$text-color: whitesmoke;

@keyframes photoButton {
  0% {
    opacity: 0;
    transform: translateY(-20px);
  }

  50% {
    opacity: 1;
    transform: translateY(15px);
  }

  100% {
    transform: translateY(0);
  }
}

* {
  box-sizing: border-box;

  margin: 0;
  padding: 0;

}

a:visited {
  color: inherit;
}

// LOGIN

.loginWrapper {
  display: flex;
}

.loginContainer {
  margin: 10% auto;
  padding: 20px;

  border-radius: 10px;
  border: 3px solid $secondary-color;
}

.loginTitle {
  text-align: center;
}

 input, textarea {
   padding: 3px;
   margin-top: 3px;
   border: 2px solid $secondary-color;
   border-radius: 5px;
}

.loginFormField .rememberMe div {
  display: inline;
  margin-right: 5px;
}

button[type='submit'] {
  background: $secondary-color;
  border-radius: 5px;
  outline: none;
  border: 2px solid transparent;
  padding: 2px 3px;
  color: whitesmoke;
  font-weight: bold;

  margin: 5px auto;
  transition: all .3s;
}

button[type='submit']:hover {
  background-color: whitesmoke;
  color: black;
  border: 2px solid $secondary-color;
  cursor: pointer;
  box-shadow: 0 0 3px 1px $secondary-color;
}

//FORMS CONTROL

.formControl.error textarea
{
  border: 2px solid $secondary-color;
}

.formControl span {
  color: black;
}


textarea {
  min-height: 54px;
  min-width: 200px;
  max-width: 300px;
  max-height: 100px;
  resize: none;
  outline: none;
}

// APP

.appWrapper {
  max-width: 70%;
  margin: 0 auto;
}

.appWrapperContent {
  width: 90%;
}

.flexContainer {
  display: flex;
  height: 100%;
  min-height: 88vh;
  margin-top: 10px;
}

